import React, { useContext } from 'react';
import { Text } from 'core/src/typography';

import TableContext from '../TableContext';
import { Cell, CellProps } from './Cell';

/** Стилизованная ячейка заголовка таблицы */
export function HeaderCell({
  children,
  ellipsis,
  ...rest
}: CellProps): JSX.Element {
  const {
    size
  } = useContext(TableContext);

  const headerChildren = (children !== undefined)
  && (typeof children === 'number'
    || typeof children === 'string')
    ? <Text color="secondary" size={ size } isEllipsis={ ellipsis }>{ children }</Text>
    : children;

  return (
    <Cell { ...rest } ellipsis={ ellipsis } component="th">
      { headerChildren }
    </Cell>
  );
}
